<template>
  <v-card :disabled="loading">
    <BlockItem>
      <span v-if="!$route.params.id" slot="title">پروژه جدید</span>
      <span v-else slot="title">ویرایش پروژه</span>
      <template v-if="!$route.params.id" slot="icon">mdi-plus</template>
      <template v-else slot="icon">mdi-pencil</template>
      <v-form ref="form">
        <v-row class="my-3" >
          <v-col class="py-0" cols="=12" md="3">
            <v-text-field
                outlined
                dense
                :rules="[$store.state.rules.required(data.title,'عنوان پروژه')]"
                append-icon="mdi-factory"
                label="عنوان پروژه"
                color="txtf"
                v-model="data.title"
            >

            </v-text-field>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-autocomplete
                outlined
                append-icon="mdi-chemical-weapon"
                item-color="info"
                dense
                :rules="[$store.state.rules.required(data.product_type,'نوع پروژه')]"
                label="نوع پروژه"
                color="txtf"
                :items="types"
                item-value="id"
                item-text="title"
                v-model="data.product_type"
            >

            </v-autocomplete>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-autocomplete
                outlined
                item-color="info"
                dense
                :rules="[$store.state.rules.required(provinceId,'استان')]"
                append-icon="mdi-earth"
                label="استان"
                color="txtf"
                no-data-text="در حال بارگزاری ..."
                item-text="name"
                item-value="id"
                v-model="provinceId"
                :items="province"
            >

            </v-autocomplete>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-autocomplete
                item-color="info"
                outlined
                dense
                append-icon="mdi-city"
                label="شهرستان"
                color="txtf"
                :rules="[$store.state.rules.required(data.location,'شهرستان')]"
                :readonly="!provinceId"
                no-data-text="در حال بارگزاری ..."
                item-text="name"
                item-value="id"
                v-model="data.location"
                :items="city"
            >

            </v-autocomplete>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-text-field
                outlined
                dense
                @keypress="isNumber($event)"
                :rules="[$store.state.rules.required(data.foundation,'مساحت')]"
                append-icon="mdi-ruler"
                label="مساحت زیر بنا"
                color="txtf"
                v-model="data.foundation"
            >

            </v-text-field>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <div class="dir-ltrF" >
              <v-text-field
                  outlined
                  dense
                  @keypress="isNumber($event)"
                  :rules="[$store.state.rules.required(data.price,'قیمت')]"
                  label="قیمت"
                  color="txtf"
                  v-model="price"
              >

                <template v-slot:append>
                  <img width="24" height="24" src="../../assets/icon-toman.svg" />
                </template>

              </v-text-field>

            </div>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <div id="date_const_date" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(date_const_date,'تاریخ شروع')]"
                  append-icon="mdi-calendar-text"
                  label="شروع رویداد"
                  color="txtf"
                  readonly
                  :value="changeIsoDate(date_const_date)"
              >

              </v-text-field>
            </div>
            <date-picker :min="new Date()" v-model="date_const_date" format="YYYY-MM-DD"
                         display-format="jYYYY-jMM-jDD" element="date_const_date"
                         :clearable="true"/>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <div id="date_expire_date" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(date_expire_date,'تاریخ پایان')]"
                  append-icon="mdi-calendar-text"
                  label="پایان رویداد"
                  color="txtf"
                  readonly
                  :value="changeIsoDate(date_expire_date)"
              >

              </v-text-field>
            </div>

            <date-picker :min="new Date(date_const_date)" :disabled="!date_const_date" v-model="date_expire_date" format="YYYY-MM-DD"
                         display-format="jYYYY-jMM-jDD" element="date_expire_date"
                         :clearable="true"/>

          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-autocomplete
                item-color="info"
                outlined
                append-icon="mdi-format-list-bulleted-type"
                v-model="data.category"
                :items="categories"
                :rules="[$store.state.rules.required(data.category,'موضوع پروژه')]"
                item-text="title"
                item-value="id"
                dense
                label="موضوع طرح"
                color="txtf"

            >

            </v-autocomplete>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-autocomplete
                outlined
                item-color="info"
                dense
                append-icon="mdi-rhombus-outline"
                label="اولویت"
                color="txtf"
                v-model="data.priority"
                :rules="[$store.state.rules.required(data.priority,'اولویت')]"
                :items="priorities"
                item-text="name"
                item-value="id"
            >

            </v-autocomplete>
          </v-col>
          <v-col class="py-0" cols="=12" md="3">
            <v-text-field
                outlined
                dense
                :rules="[$store.state.rules.required(data.material,'مواد')]"
                append-icon="mdi-checkerboard"
                label="مواد"
                color="txtf"
                v-model="data.material"
            >

            </v-text-field>
          </v-col>
          <v-col class="py-0" cols="=12" md="12">
            <v-textarea
                outlined
                rows="1"
                dense
                label="توضیحات"
                color="txtf"
                v-model="data.desc"
            >

            </v-textarea>
          </v-col>
          <v-col class="py-0" cols="=12" md="12">
            <UploadFile v-if="renderComponent" :items="files" />
          </v-col>
        </v-row>
      </v-form>
      <template v-slot:ltop>
        <v-btn v-if="!$route.params.id && $store.state.permissions && $store.state.permissions['create-product']" class="dash-btns dash-btns--blue" :loading="loading" dark height="32" @click="createProduct()"  >ایجاد پروژه</v-btn>
        <v-btn v-else-if="$route.params.id && $store.state.permissions && $store.state.permissions['change-product']" class="dash-btns dash-btns--blue" :loading="loading" dark height="32" @click="editProduct()" >ویرایش پروژه</v-btn>
      </template>
    </BlockItem>
  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import UploadFile from "./DashAddProject/UploadFile.vue";
import datePicker from 'vue-persian-datetime-picker'
import {datesChanges} from "@/components/Misc/datesChanges";
import {isChars} from "@/components/Misc/isChars";
import {forceRerender} from "@/components/Misc/forceRerender";
import numeral from 'numeral';
import axios from "axios";


export default {

  name: "DashProjectAdd",
  created() {
    if(this.$route.params.id){
      this.fetchProdcut();
    }
    this.fetchProvince();
    this.fetchState();
    this.fetchPriorities();
    this.fetchTypes();
  },
  components: {BlockItem,UploadFile,datePicker},
  mixins : [datesChanges,isChars,forceRerender],
  data() {
    return {
      files : [],
      city : null,
      province : null,
      provinceId : null,
      price : null,
      categories : null,
      priorities : null,
      types : null,
      date_const_date : null,
      date_expire_date : null,
      loading : false,
      data: {
        title: null,
        files : [],
        material : null,
        desc : null,
        location : null,
        product_type : null,
        priority : null,
        price : null,
        foundation : null,
        date_const_date : null,
        date_expire_date : null,
        category : null,
      }
    }
  },
  methods : {
    editProduct() {
      this.$refs.form.validate();
      if(this.$refs.form.validate()) {
        this.loading = true;
        this.data.files = this.files.map(item => item.id);
        this.data.date_expire_date = new Date(this.date_expire_date).getTime()/1000;
        this.data.date_const_date = new Date(this.date_const_date).getTime()/1000;

        axios.patch(this.$store.state.host + '/projects/api/v1/product/change-product/'+this.$route.params.id,this.data,{
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(()=>{
          this.notify('پروژه شما ویرایش شد','info');
          this.$router.push({name:'DashProjects'})
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    createProduct(){
      this.$refs.form.validate();
      if(this.$refs.form.validate()) {
        this.loading = true;
        this.data.files = this.files.map(item => item.id);
        this.data.date_expire_date = new Date(this.date_expire_date).getTime()/1000;
        this.data.date_const_date = new Date(this.date_const_date).getTime()/1000;

        axios.post(this.$store.state.host + '/projects/api/v1/product/create-product',this.data,{
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(()=>{
          this.notify('پروژه شما ایجاد شد','success');
          this.$router.push({name:'DashProjects'})
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false;
        })
      }

    },
    fetchProdcut () {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/product/single-product/'+this.$route.params.id,{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        const data = response.data.data;
        this.files = data.files;
        this.forceRerender();
        this.data.priority = data.priority.id;
        this.provinceId = data.location.province.id;
        this.data.location = data.location.id;

        console.log(data)
        this.data.desc = data.desc;
        this.data.material = data.material;
        this.data.title = data.title;
        this.date_const_date = this.persianDateToEn(data.date_const_date)
        this.date_expire_date = this.persianDateToEn(data.date_expire_date)
        this.price = data.price;
        this.data.foundation = data.foundation;
        this.data.product_type = data.product_type.id;

        this.data.category = data.category.id;



      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    },
    fetchPriorities() {
      axios.get(this.$store.state.host + '/projects/api/v1/priority/priorities',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.priorities = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    fetchTypes() {
      axios.get(this.$store.state.host + '/projects/api/v1/type/types',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.types = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    fetchState() {
      axios.get(this.$store.state.host + '/projects/api/v1/category/categories',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.categories = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    fetchProvince () {
      axios.get(this.$store.state.host + '/projects/api/v1/province/provinces').then(response=>{
        this.province = response.data.data
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    fetchCity (id) {
      axios.get(this.$store.state.host + '/projects/api/v1/city/cities/'+id).then(response=>{
        this.city = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
  },
  watch : {
    date_const_date(){
      if(!this.loading) {
        this.date_expire_date = null;
      }
    },
    'price'(val) {
      if (this.price) {
        this.price = numeral(val).format('0,0');
        this.data.price = numeral(this.price).value();
      } else {
        this.data.price = null;
      }
    },
    'provinceId' (val) {
      this.fetchCity(val);
    }
  }
}
</script>

